<template>
    <div style="text-align: left">
        <h3>成员资料编辑</h3>
        <Divider orientation="left">信息填写</Divider>
        <Row>
            <Col style="width: 500px">
                <Form :model="formData" :label-width="120">
                    <FormItem label="姓名">
                        <Input v-model="formData.name" placeholder="请填写姓名"></Input>
                    </FormItem>
                    <FormItem label="职务">
                        <Input v-model="formData.position" placeholder="请填写职务"></Input>
                    </FormItem>
                    <FormItem label="签名">
                        <Input v-model="formData.signature" placeholder="请填写签名"></Input>
                    </FormItem>
                    <FormItem label="主要成就">
                        <Input v-model="formData.achievement" type="textarea" :rows="5" placeholder="请填写主要成就"></Input>
                    </FormItem>
                    <FormItem label="个人履历	">
                        <Input v-model="formData.resume" type="textarea" :rows="5" placeholder="请填写个人履历"></Input>
                    </FormItem>
                    <FormItem label="形象照片">
                        <ImgSelect v-model="picture" :url="formData.picture">
                        </ImgSelect>
                    </FormItem>

                    <FormItem>
                        <Button type="success" long @click="submit">提交</Button>
                    </FormItem>
                </Form>
                <Spin size="large" fix v-if="load"></Spin>
            </Col>

        </Row>
    </div>
</template>

<script>
  import api from './api';
  import ImgSelect from '@/components/imgSelect';
  import ImgsSelect from '@/components/imgsSelect';

  export default {
    name: 'edit',
    components: {
      ImgSelect,
      ImgsSelect
    },
    data() {
      return {
        formData: {},
        picture: null,
        load: false,
      };
    },
    mounted() {
      if (this.$route.query.id != null) {
        this.loadData();
      }
    },
    methods: {
      loadData() {
        this.load = true;
        api.detail({ id: this.$route.query.id }, res => {
          this.load = false;
          this.formData = res.data.data;
          if (this.formData.status === 0) {
            this.$router.push({
              name: 'memberDetail',
              params: { id: this.formData.id }
            });
            this.$Message.error('资料审核中不能修改');
          }
        }, () => {
          this.load = false;
        });
      },
      submit() {
        if (this.picture == null && !this.$formUtil.isStrValid(this.formData.picture)) {
          this.$Message.error('请选择 个人照片');
          return;
        }
        this.formData.enter_team_id=this.$store.state.user.enter_team_id;
        let data = this.$formUtil.objToFormData(this.formData);
        if (this.picture != null) data.append('picture', this.picture);
        this.load = true;
        api.save(data, () => {
          this.load = false;
          this.$router.back();
        }, () => {
          this.load = false;
        });
      }
    }
  };
</script>

