<template>
    <label>
        <span>
            <span class="upload-img"></span>
            <input type="file" ref="path" accept="image/*" style="display: none" @change="getFile"/>
            <span v-show="picture" class="img-preview">
                <img :src="picture" width="90px" height="90px"/>
            </span>
        </span>
    </label>
</template>

<script>
  export default {
    name: 'imgSelect',
    props: ['value', 'url'],
    data() {
      return {
        img: null,
        picture: null
      };
    },
    watch: {
      url(value) {
        this.picture = this.$url + value;
      }
    },
    methods: {
      getFile(e) {
        let file = e.target.files[0];
        if (file != null) {
          this.img = file;
          this.imgPreview(file);
          this.$emit('input', this.img);
        }
        this.$refs.path.value = '';
      },
      imgPreview(file) {
        let self = this;
        if (!file || !window.FileReader) return;
        if (/^image/.test(file.type)) {
          let reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onloadend = function () {
            self.picture = this.result;
          };
        }
      }
    }
  };
</script>

<style scoped>
    .upload-img {
        background: url("../assets/imgUpload.png");
        background-size: 100% 100%;
        text-align: center;
        width: 100px;
        display: inline-block;
        height: 100px;
        margin-right: 20px;
    }

    .img-preview {
        border: 1px dashed #d4d4d4;
        display: inline-block;
        width: 100px;
        padding: 5px;
        height: 100px;
    }

</style>
