<template>
        <span>
          <label v-show="pictures.length===0">
            <span class="upload-img"></span>
            <input type="file" ref="path" accept="image/*" style="display: none" @change="getFile" multiple/>
          </label>
            <span v-show="pictures" v-for="(item,index) in pictures" v-bind:key="index" class="img-preview">
                <img :src="item" width="90px" height="90px" style="margin: 5px"/>
            </span>
           <Button v-show="pictures.length!==0" @click="remove" type="error" class="reSelect"
                   icon="md-trash">清空已选</Button>
        </span>
</template>

<script>
  export default {
    name: 'imgsSelect',
    props: ['value', 'urls', 'max'],
    data() {
      return {
        pictures: []
      };
    },
    watch: {
      urls(value) {
        value.forEach(val => {
          this.pictures.push(this.$url + val);
        });
      }
    },
    methods: {
      getFile(e) {
        this.pictures = [];
        let files = e.target.files;
        if (files.length > this.max) {
          this.$Message.error('超过' + this.max + '张,请重新选择');
          return;
        }
        if (files != null) {
          this.imgPreview(files);
          this.$emit('input', files);
        }
      },
      remove() {
        this.$emit('input', []);
        this.pictures = [];
      },
      imgPreview(files) {
        let _this = this;
        if (!files || !window.FileReader) return;
        console.log(files);
        for (let i = 0; i < files.length; i++) {
          let file = files[i];
          if (/^image/.test(file.type)) {
            let reader = new FileReader();
            reader.onloadend = function (e) {
              let img = e.target.result;
              _this.pictures.push(img);
            };
            reader.readAsDataURL(file);
          }
        }
        console.log(this.imgs);
      }
    }
  };
</script>

<style scoped>
    .upload-img {
        background: url("../assets/imgUpload.png");
        background-size: 100% 100%;
        text-align: center;
        width: 100px;
        display: inline-block;
        height: 100px;
        margin-right: 20px;
    }

    .img-preview {
        display: inline-block;
        margin-right: 20px;
        width: 100px;
        height: 100px;
        position: relative;
        border: 1px dashed #d4d4d4;

    }

    .reSelect {
        display: block;
    }
</style>
